.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
html,
body {
  margin: 0;
  padding: 0;
  background-color: #bdc3c7;
  font-family: fantasy;
}

.tic-tac-toe {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
}

.board-row {
  display: flex;
}

.board-square {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 48px;
  width: 100px;
  height: 100px;
  margin: 5px;
  background-color: #95a5a6;
  color: #fff;
  text-shadow: 2px 2px #000;
  border-radius: 10px;
  box-shadow: 2px 2px 5px #000;
  cursor: pointer;
}

.board-square:hover {
  background-color: #7f8c8d;
}

.message {
  margin-top: 20px;
  font-size: 36px;
  color: #fff;
  text-shadow: 2px 2px #000;
}

button {
  margin-top: 20px;
  font-size: 24px;
  color: #fff;
  text-shadow: 2px 2px #000;
  background-color: #2980b9;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  cursor: pointer;
}

button:hover {
  background-color: #2c3e50;
}